import { AfterViewInit, Component, Input, ViewChild } from '@angular/core';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { StoricoCliente, WebApiResponse } from 'src/app/shared/models/dto';
import { TipoStoricoCliente } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-storico-cliente',
  templateUrl: './storico-cliente.component.html',
  styleUrls: ['./storico-cliente.component.scss']
})
export class StoricoClienteComponent extends BaseComponent implements AfterViewInit {

  @Input() oidCliente: number;
  @Input() small: boolean = false;

  @ViewChild(MatPaginator) paginator: MatPaginator;

  public displayedColumns: any = ['tipo', 'codice', 'data', 'arrivo', 'partenza', 'stato', 'totale', 'openEdit'];
  public dataSource: MatTableDataSource<StoricoCliente>;
  public enumTipoStoricoCliente = TipoStoricoCliente;

  public richieste: boolean = true;
  public preventivi: boolean = true;
  public prenotazioni: boolean = true;

  constructor(private apiService: ApiService, private utility: UtilityService) { super(); }

  ngAfterViewInit() {
    if (this.oidCliente != null && this.oidCliente > 0) {
      this.setIsLoading(true);

      this.apiService.getStoricoCliente(this.oidCliente).subscribe((rs: WebApiResponse) => {
        this.dataSource = new MatTableDataSource(rs.data);

        this.dataSource.filterPredicate = (data: StoricoCliente, filter: string) => {
          let f: TipoStoricoCliente[] = null;

          if (!!filter) {
            try {
              f = JSON.parse(filter);
            } catch (e) { }
          }

          return f == null || f.includes(data.tipo);
        };

        setTimeout(() => {
          this.dataSource.paginator = this.paginator;
        }, 200);

        this.setIsLoading(false);
      });
    }
  }

  onEdit(storico: StoricoCliente) {
    let url: string;

    if (storico.tipo == TipoStoricoCliente.Richiesta) {
      url = '/sbm/home/(homeOutlet:richieste)?oidRichiesta=';
    } else if (storico.tipo == TipoStoricoCliente.Preventivo) {
      url = '/sbm/home/(homeOutlet:preventivi)?oidPreventivo=';
    } else if (storico.tipo == TipoStoricoCliente.Prenotazione) {
      url = '/sbm/home/(homeOutlet:prenotazioni)?oidPrenotazione=';
    }

    this.utility.openUrlInNewTab(url + storico.oid);
  }

  onOpen(storico: StoricoCliente) {
    let url: string;

    if (storico.tipo == TipoStoricoCliente.Preventivo) {
      url = '/booking/mypage?uid=';
    } else if (storico.tipo == TipoStoricoCliente.Prenotazione) {
      url = '/booking/self-checkin?uid=';
    }

    this.utility.openUrlInNewTab(url + storico.uid);
  }

  onFilterChange() {
    let filter: TipoStoricoCliente[] = [];

    if (this.richieste) {
      filter.push(TipoStoricoCliente.Richiesta);
    }

    if (this.preventivi) {
      filter.push(TipoStoricoCliente.Preventivo);
    }

    if (this.prenotazioni) {
      filter.push(TipoStoricoCliente.Prenotazione);
    }

    this.dataSource.filter = JSON.stringify(filter);
  }
}