import { animate, state, style, transition, trigger } from '@angular/animations';
import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import { MatDatepicker } from '@angular/material/datepicker';
import { CalendarMonthViewDay } from 'angular-calendar';
import * as moment from 'moment';
import { Subject, Subscription } from 'rxjs';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { RichiesteAddComponent } from 'src/app/interfaces/richieste-add/richieste-add.component';
import { ViewSelectDialogComponent } from 'src/app/interfaces/view-select/view-select-dialog/view-select-dialog.component';
import { Cliente, CodicePromozione } from 'src/app/shared/models/bo.model';
import {
  BookingReductionConfigRS,
  BookingSolutionRoomRQ,
  BookingSolutionRQ,
  BookingTravelReasonConfigRS,
  CameraBookingUrlParameter,
  DialogOptions,
  Dimension,
  DisponibilitaNotti,
  Errore,
  GetCodiciPromozioneValidiRQ,
  GetDisponibilita,
  GetDisponibilitaNottiRQ,
  GetDisponibilitaRQ,
  GetDisponibilitaRS,
  GetPrezzoMinimoRQ,
  TranslateParameter,
  WebApiResponse,
} from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-booking-calendar',
  templateUrl: './booking-calendar.component.html',
  styleUrls: ['./booking-calendar.component.scss'],
  animations: [
    trigger('blink', [
      state('light', style({
        backgroundColor: '#006DC9'
      })),
      state('dark', style({
        backgroundColor: '#004987'
      })),
      transition('light => dark', [
        animate('500ms ease-out')
      ]),
      transition('dark => light', [
        animate('500ms ease-in')
      ])
    ])
  ]
})
export class BookingCalendarComponent extends BaseComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() oidAlbergo: number;
  @Input() oidPortale: number;
  @Input() lingua: string;
  @Input() isVisualizzaDisponibilitaCalendario: boolean;
  @Input() isVisualizzaPrezzoCalendario: boolean;
  @Input() email: string;
  @Input() cellulare: string;
  @Input() mappa: string;
  @Input() clickSubject: Subject<void>;
  @Input() dayUse: boolean;
  @Input() adultiDefault: number;
  @Input() adultiMax: number;
  @Input() bambiniMax: number;
  @Input() etaMinimaBambini: number;
  @Input() etaMassimaBambini: number;
  @Input() camereUrl: CameraBookingUrlParameter[];
  @Input() arrivoUrl: string;
  @Input() partenzaUrl: string;
  @Input() codicePromozioneUrl: string;
  @Input() controllingDisponibility: boolean;
  @Input() isEtaRiduzioniAttiva: boolean;
  @Input() riduzioni: BookingReductionConfigRS[];
  @Input() isBooking: boolean = true;
  @Input() isRichiediDisponibilitaStep1Visualizzata: boolean;
  @Input() numeroCodiciPromozionali: number;
  @Input() motiviViaggio: BookingTravelReasonConfigRS[];
  @Input() camerePrenotabili: number;
  @Input() isVisualizzaCalendarioMobile: boolean;
  @Input() showSearchButtonSubject: Subject<void>;
  @Input() isVisualizzaPrezzoMinimo: boolean;
  @Input() hideButton: boolean = true;
  @Input() oidTipoCameraOTA: number;
  @Input() oidTariffaOTA: number;
  @Input() valuta: string;
  @Input() isDisattivaRichiestaBambini: boolean;
  @Input() isControllaDurateSoggiornoSulGiornoDiArrivo: boolean;
  @Input() isProponiNottiCalendario: boolean;
  @Input() initialDate: string;

  @Input() set codicePromozione(value: string) {
    if (this.rq != null) {
      if (!!value) {
        this.previousCodicePromozione = this.rq.codicePromozione;
        this.rq.codicePromozione = value;
      } else {
        this.rq.codicePromozione = this.previousCodicePromozione;
      }

      this.getPrezzoMinimo();
      this.showSearchButton();
    }
  };

  @Output() daysSelected: EventEmitter<BookingSolutionRQ> = new EventEmitter();

  @ViewChild('arrivo') datePickerArrivo: MatDatepicker<any>;
  @ViewChild('partenza') datePickerPartenza: MatDatepicker<any>;
  @ViewChild('emailLabel') emailLabel: ElementRef;

  public rq: BookingSolutionRQ;
  public date: Date;
  public selectableMonths: moment.Moment[] = [];
  public startDate: string;
  public dateClicked: boolean = false;
  public disponibilita: GetDisponibilita[] = [];
  public previousDisabled: boolean = true;
  public days: CalendarMonthViewDay[];
  public refresh: Subject<void> = new Subject();
  public tooltips: Map<string, string> = new Map<string, string>();
  public minPrices: Map<string, number> = new Map<string, number>();
  public numeroMinimoGiorniSelectedDaysPast: number = 0;
  public numeroMassimoGiorniSelectedDaysPast: number = null;
  public notAvailablePast: boolean = false;
  public numeroMinimoGiorniSelectedDays: number = 0;
  public numeroMassimoGiorniSelectedDays: number = null;
  public notAvailable: boolean = false;
  public nights: number;
  public addFromButtonDisabled: boolean;
  public minusFromButtonDisabled: boolean;
  public addToButtonDisabled: boolean;
  public minusToButtonDisabled: boolean;
  public prezzoMinimo: number = null;
  public mobileBreakpoint: number = 992;
  public sub: Subscription;
  public sub1: Subscription;
  public sub2: Subscription;
  public previousWindowSize: number;
  public arrivoDate: string;
  public partenzaDate: string;
  public urlSearchParametersReceived: boolean = false;
  public prezzoMinimoRequest: Subscription;
  public errors: Errore[];
  public previousCodicePromozione: string;
  public prezzoCalculated: boolean;
  public notAvailableDate: string;
  public searchButtonVisible: boolean = true;
  public blinkStatus: string;
  public emailTooltipVisible: boolean = false;
  public showNightsSelect: boolean;
  public disponibilitaNotti: DisponibilitaNotti[];

  constructor(private apiService: ApiService,
    public translationService: LocalizationService,
    public containerService: ContainerService,
    private sessionService: SessionService,
    public utility: UtilityService) {
    super();
  }

  ngOnInit() {
    this.setIsLoading(true);

    if (!!this.initialDate) {
      this.date = this.utility.toDate(moment(this.initialDate));
    } else {
      this.date = this.utility.toDate(moment());
    }

    this.rq = new BookingSolutionRQ(this.adultiDefault);
    this.rq.isBooking = this.isBooking;

    let date = moment();
    date.set('date', 1);

    for (let i = 0; i < 12; i++) {
      let d = moment(date);
      this.selectableMonths.push(d);
      d.add(i, 'month');
    }

    if (this.clickSubject != null) {
      this.sub1 = this.clickSubject.subscribe(() => {
        if (this.dateClicked && !!this.startDate && !this.showNightsSelect) {
          this.onSelectionError();
          this.notAvailableDate = null;
        }
      });
    }

    if (this.showSearchButtonSubject != null) {
      this.sub2 = this.showSearchButtonSubject.subscribe(() => {
        this.showSearchButton();
      });
    }

    this.sub = this.containerService.windowResizeEvent.subscribe((d: Dimension) => {
      if (this.datePickerArrivo != null) {
        this.datePickerArrivo.close();
      }

      if (this.datePickerPartenza != null) {
        this.datePickerPartenza.close();
      }

      if (this.previousWindowSize != null && this.previousWindowSize < this.mobileBreakpoint && d.width >= this.mobileBreakpoint && !this.isVisualizzaCalendarioMobile) {
        this.refresh.next();

        if (!!this.rq.arrivo) {
          this.date = this.utility.toDate(moment(this.rq.arrivo));
        }

        if (!!this.rq.arrivo && !!this.rq.partenza) {
          this.getPrezzoMinimo();
        }
      }

      if (d.width < this.mobileBreakpoint && !this.isVisualizzaCalendarioMobile) {
        this.checkDisabledButtonsMobile();
      }

      this.previousWindowSize = d.width;
    });

    this.previousWindowSize = this.containerService.dimension.width;

    this.rq.codicePromozione = this.codicePromozioneUrl;

    if (!!this.arrivoUrl && !!this.partenzaUrl) {
      this.rq.arrivo = this.arrivoUrl;
      this.rq.partenza = this.partenzaUrl;
      this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);
      this.urlSearchParametersReceived = true;
      this.date = this.utility.toDate(moment(this.rq.arrivo));
    } else if (this.containerService.dimension.width < this.mobileBreakpoint && !this.isVisualizzaCalendarioMobile) {
      if (this.dayUse) {
        this.rq.arrivo = this.getDate(moment());
        this.rq.partenza = this.rq.arrivo;
      } else {
        this.rq.arrivo = this.getDate(moment());
        this.rq.partenza = this.getDate(moment().add(1, 'day'));
      }

      this.nights = 1;

      //DA CONTROLLARE
      this.urlSearchParametersReceived = true;

      this.checkDisabledButtonsMobile();
    }
  }

  ngAfterViewInit() {
    setTimeout(() => {
      if (this.emailLabel != null) {
        this.emailTooltipVisible = this.emailLabel.nativeElement.offsetWidth < this.emailLabel.nativeElement.scrollWidth;
      }
    }, 200);
  }

  onRender(days: CalendarMonthViewDay[]) {
    if (this.isVisualizzaDisponibilitaCalendario || this.isVisualizzaPrezzoCalendario) {
      if (this.isVisualizzaDisponibilitaCalendario) {
        if (this.dateClicked && !!this.startDate) {
          if (days[0].date.getFullYear() < this.days[0].date.getFullYear()) {
            this.dayHover(this.days[0]);
          } else if (days[0].date.getFullYear() > this.days[0].date.getFullYear()) {
            this.dayHover(this.days[this.days.length - 1]);
          } else {
            if (days[0].date.getMonth() < this.days[0].date.getMonth()) {
              this.dayHover(this.days[0]);
            } else if (days[0].date.getMonth() > this.days[0].date.getMonth()) {
              this.dayHover(this.days[this.days.length - 1]);
            }
          }

          this.notAvailablePast = this.notAvailable;
          this.numeroMinimoGiorniSelectedDaysPast = this.numeroMinimoGiorniSelectedDays;
          this.numeroMassimoGiorniSelectedDaysPast = this.numeroMassimoGiorniSelectedDays;
        } else {
          if (!this.urlSearchParametersReceived) {
            this.controlDays();
          }
        }
      }

      this.getDisponibilita(days);
    } else {
      this.days = days;
      this.disablePastDays(days);
      this.refreshSelection(days);

      if (!this.startDate && !this.dateClicked) {
        this.checkDisabledButtons();
      }

      setTimeout(() => {
        if (!this.controllingDisponibility) {
          this.setIsLoading(false);
        }
      }, 200);
    }

    setTimeout(() => {
      window.dispatchEvent(new Event('resize'));

      let today = moment();

      if (this.date.getMonth() <= today.month() && this.date.getFullYear() <= today.year()) {
        this.previousDisabled = true;
      } else {
        this.previousDisabled = false;
      }
    }, 200);
  }

  dayClicked(day: CalendarMonthViewDay) {
    let date = this.getDate(day.date);

    if (this.dateClicked) {
      if (this.startDate != date) {
        this.rq.arrivo = moment(date) < moment(this.startDate) ? date : this.startDate;
        this.rq.partenza = moment(date) < moment(this.startDate) ? this.startDate : date;

        if (this.errors == null || this.errors.length == 0) {
          this.dateClicked = false;
          this.startDate = null;
          this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);

          for (let d of this.days) {
            if (!!d.cssClass) {
              d.cssClass = d.cssClass.replace('cal-day-outlined', 'cal-day-selected');
            }
          }

          this.showSearchButton();
          this.checkDisabledButtons();
        } else {
          this.rq.arrivo = null;
          this.rq.partenza = null;
        }
      } else {
        this.onSelectionError();
      }
    } else {
      this.urlSearchParametersReceived = false;

      if ((day.cssClass == null || (day.cssClass != null && !day.cssClass.includes('not-available'))) && moment(date) >= moment().startOf('day')) {
        if (!this.dayUse) {
          this.rq.arrivo = null;
          this.rq.partenza = null;
          this.nights = null;
          this.startDate = date;
          this.dateClicked = true;
          this.resetSelection();
          day.cssClass = this.setCssClass(day.cssClass, 'cal-day-outlined first-selected-day last-selected-day');
          this.daysControlReset();
          this.prezzoMinimo = null;

          if (this.isProponiNottiCalendario) {
            this.setIsLoading(true);

            let getDisponibilitaNottiRQ = new GetDisponibilitaNottiRQ();
            getDisponibilitaNottiRQ.oidPortale = this.oidPortale;
            getDisponibilitaNottiRQ.arrivo = date;

            this.apiService.getDisponibilitaNotti(getDisponibilitaNottiRQ).subscribe(getDisponibilitaNottiRS => {
              this.disponibilitaNotti = getDisponibilitaNottiRS.data;
              this.showNightsSelect = true;
              this.setIsLoading(false);
            });
          }
        } else {
          this.rq.arrivo = date;
          this.rq.partenza = date;

          let errors: Errore[] = null;

          if (this.isVisualizzaDisponibilitaCalendario) {
            errors = this.controlDays();
          }

          if (errors == null || errors.length == 0) {
            this.getPrezzoMinimo();
            this.dateClicked = false;
            this.startDate = null;
            this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);
            this.resetSelection();
            day.cssClass = this.setCssClass(day.cssClass, 'cal-day-outlined first-selected-day last-selected-day');
            this.showSearchButton();
            this.checkDisabledButtons();
          } else {
            this.rq.arrivo = null;
            this.rq.partenza = null;
            this.daysControlReset();
          }
        }
      }
    }
  }

  onSelectionError() {
    this.rq.arrivo = null;
    this.rq.partenza = null;
    this.nights = null;
    this.dateClicked = false;
    this.startDate = null;
    this.prezzoMinimo = null;
    this.resetSelection();
    this.daysControlReset();
  }

  daysControlReset() {
    this.numeroMinimoGiorniSelectedDays = 0;
    this.numeroMassimoGiorniSelectedDays = null;
    this.notAvailable = false;
    this.numeroMinimoGiorniSelectedDaysPast = 0;
    this.numeroMassimoGiorniSelectedDaysPast = null;
    this.notAvailablePast = false;
  }

  daysControlResetCurrent() {
    this.numeroMinimoGiorniSelectedDays = 0;
    this.numeroMassimoGiorniSelectedDays = null;
    this.notAvailable = false;
  }

  controlDays(endDate?: string, arrivo?: string, partenza?: string): Errore[] {
    if ((!!this.rq.arrivo && !!this.rq.partenza) || (this.dateClicked && !!this.startDate && !!endDate) || (!!arrivo && !!partenza)) {
      if (this.notAvailablePast) {
        let error = new Errore();
        error.idTraduzione = 'booking.periodoNonDisponibile';
        return [error];
      } else {
        this.notAvailableDate = null;
      }

      let from: string = null;
      let to: string = null;

      if (this.dateClicked && !!this.startDate && !!endDate) {
        from = moment(this.startDate) < moment(endDate) ? this.startDate : endDate;
        to = moment(this.startDate) < moment(endDate) ? endDate : this.startDate;
      } else if (!!this.rq.arrivo && !!this.rq.partenza) {
        from = this.rq.arrivo;
        to = this.rq.partenza;
      } else {
        from = arrivo;
        to = partenza;
      }

      if (!this.dayUse) {
        to = this.getDate(moment(to).subtract(1, 'day'));
      }

      let daysSelectedCount: number = 0;
      let numeroMinimoGiorniSelectedDays = this.numeroMinimoGiorniSelectedDaysPast;
      let numeroMassimoGiorniSelectedDays = this.numeroMassimoGiorniSelectedDaysPast;

      for (let d = from; moment(d) <= moment(to); d = this.getDate(moment(d).add(1, 'day'))) {
        let index = this.days.findIndex(x => this.getDate(x.date) == d);

        if (index >= 0) {
          if (!!this.days[index].cssClass) {
            if (this.days[index].cssClass.includes('not-available')) {
              this.notAvailableDate = this.getDate(this.days[index].date);
              this.notAvailable = true;
              let error = new Errore();
              error.idTraduzione = 'booking.periodoNonDisponibile';
              return [error];
            } else if (this.days[index].cssClass.includes('limitations')) {
              if (!this.isControllaDurateSoggiornoSulGiornoDiArrivo || d == from) {
                let disp = this.disponibilita.find(x => this.getDate(x.data) == d);

                numeroMinimoGiorniSelectedDays = Math.max(numeroMinimoGiorniSelectedDays, disp.durataMinimaSoggiorno);

                if (numeroMassimoGiorniSelectedDays == null) {
                  numeroMassimoGiorniSelectedDays = disp.durataMassimaSoggiorno;
                } else {
                  numeroMassimoGiorniSelectedDays = Math.min(numeroMassimoGiorniSelectedDays, disp.durataMassimaSoggiorno);
                }
              }
            }
          }
        }

        daysSelectedCount++;
      }

      this.numeroMinimoGiorniSelectedDays = numeroMinimoGiorniSelectedDays;
      this.numeroMassimoGiorniSelectedDays = numeroMassimoGiorniSelectedDays;

      if ((this.numeroMinimoGiorniSelectedDays > 0 && daysSelectedCount < this.numeroMinimoGiorniSelectedDays) || (this.numeroMassimoGiorniSelectedDays > 0 && daysSelectedCount > this.numeroMassimoGiorniSelectedDays)) {
        let errori: Errore[] = [];

        if (this.numeroMinimoGiorniSelectedDays > 0 && daysSelectedCount < this.numeroMinimoGiorniSelectedDays) {
          errori.push(new Errore('booking.durataMinimaSoggiorno', [new TranslateParameter(this.numeroMinimoGiorniSelectedDays.toString())]));
        }

        if (this.numeroMassimoGiorniSelectedDays > 0 && daysSelectedCount > this.numeroMassimoGiorniSelectedDays) {
          errori.push(new Errore('booking.durataMassimaSoggiorno', [new TranslateParameter(this.numeroMassimoGiorniSelectedDays.toString())]));
        }

        return errori;
      }
    }

    return null;
  }

  dayHover(day: CalendarMonthViewDay) {
    if (this.startDate != null && this.dateClicked) {
      let date = this.getDate(day.date);
      this.resetSelection();

      let arrivo = moment(date) < moment(this.startDate) ? date : this.startDate;
      let partenza = moment(date) < moment(this.startDate) ? this.startDate : date;

      this.getPrezzoMinimo(arrivo, partenza);

      if (this.isVisualizzaDisponibilitaCalendario) {
        this.daysControlResetCurrent();
        this.errors = this.controlDays(null, arrivo, partenza);
      }

      this.nights = Math.max(moment(partenza).diff(moment(arrivo), 'days'), 1);

      for (let d = arrivo; d <= partenza; d = this.getDate(moment(d).add(1, 'day'))) {
        let index = this.days.findIndex(x => this.getDate(x.date) == d);

        if (index >= 0) {
          if (!!this.notAvailableDate && moment(d) > moment(this.notAvailableDate)) {
            this.days[index].cssClass = this.setCssClass(this.days[index].cssClass, 'not-selectable');
          }

          if (d == (moment(date) < moment(this.startDate) ? date : this.startDate)) {
            this.days[index].cssClass = this.setCssClass(this.days[index].cssClass, 'first-selected-day');
          }

          if (d == (moment(date) < moment(this.startDate) ? this.startDate : date)) {
            this.days[index].cssClass = this.setCssClass(this.days[index].cssClass, 'last-selected-day');
          }

          this.days[index].cssClass = this.setCssClass(this.days[index].cssClass, 'cal-day-outlined');
        }
      }
    }
  }

  resetSelection() {
    this.prezzoMinimo = null;

    if (this.days != null) {
      for (let d of this.days) {
        if (!!d.cssClass) {
          d.cssClass = d.cssClass.replace('cal-day-outlined', '');
          d.cssClass = d.cssClass.replace('cal-day-selected', '');

          d.cssClass = d.cssClass.replace('first-selected-day', '');
          d.cssClass = d.cssClass.replace('last-selected-day', '');

          d.cssClass = d.cssClass.replace('not-selectable', '');

          d.cssClass = d.cssClass.replace(/  +/g, ' ');
        }

        if (d.cssClass == '' || d.cssClass == ' ') {
          delete d.cssClass;
        }
      }
    }
  }

  refreshSelection(days: CalendarMonthViewDay[]) {
    if (!!this.rq.arrivo && !!this.rq.partenza) {
      for (let d = this.rq.arrivo; moment(d) <= moment(this.rq.partenza); d = this.getDate(moment(d).add(1, 'day'))) {
        let index = days.findIndex(x => this.getDate(x.date) == d);

        if (index >= 0) {
          if (d == this.rq.arrivo) {
            days[index].cssClass = this.setCssClass(days[index].cssClass, 'first-selected-day');
          }

          if (d == this.rq.partenza) {
            days[index].cssClass = this.setCssClass(days[index].cssClass, 'last-selected-day');
          }

          days[index].cssClass = this.setCssClass(days[index].cssClass, 'cal-day-selected');
        }
      }
    }
  }

  disablePastDays(days: CalendarMonthViewDay[]) {
    for (let d = this.getDate(days[0].date); moment(d) < moment().startOf('day'); d = this.getDate(moment(d).add(1, 'day'))) {
      let index = days.findIndex(x => this.getDate(x.date) == d);

      if (index >= 0) {
        days[index].cssClass = this.setCssClass(days[index].cssClass, 'cal-out-month disabled-day');
      }
    }
  }

  getDisponibilita(days: CalendarMonthViewDay[]) {
    this.setIsLoading(true);
    let rq = new GetDisponibilitaRQ();
    rq.dal = this.getDate(days[0].date);
    rq.al = this.getDate(days[days.length - 1].date);
    rq.oidPortale = this.oidPortale;
    rq.dayUse = this.dayUse;

    this.apiService.getDisponibilita(rq).subscribe((res: GetDisponibilitaRS) => {
      this.disponibilita = res.data.disponibilita;
      this.tooltips.clear();
      this.minPrices.clear();

      for (let day of res.data.disponibilita) {
        if (this.isVisualizzaDisponibilitaCalendario) {
          let index = days.findIndex(x => this.getDate(x.date) == this.getDate(day.data));

          if (index >= 0) {
            days[index].cssClass = this.setCssClass(days[index].cssClass, day.cssClass);
          }

          if (!!day.cssClass) {
            if (day.cssClass.includes('available')) {
              this.tooltips.set(this.getDate(day.data), this.translationService.getTranslationSync('booking.disponibile'));
            } else if (day.cssClass.includes('not-available')) {
              this.tooltips.set(this.getDate(day.data), this.translationService.getTranslationSync('booking.nonDisponibile'));
            } else if (day.cssClass.includes('limitations')) {
              let ret: string = '';

              if (day.durataMinimaSoggiorno > 0) {
                ret = this.translationService.getTranslationSync('booking.durataMinimaSoggiorno', { value: day.durataMinimaSoggiorno });
              }

              if (day.durataMassimaSoggiorno > 0) {
                let durataMassimaString = this.translationService.getTranslationSync('booking.durataMassimaSoggiorno', { value: day.durataMassimaSoggiorno });

                if (!ret) {
                  ret = durataMassimaString;
                } else {
                  ret += '\n' + durataMassimaString;
                }
              }

              this.tooltips.set(this.getDate(day.data), ret);
            }
          }
        }

        if (this.isVisualizzaPrezzoCalendario) {
          this.minPrices.set(this.getDate(day.data), day.prezzoMin);
        }
      }

      this.days = days;

      if (this.isVisualizzaDisponibilitaCalendario && !this.urlSearchParametersReceived) {
        let errors: Errore[] = null;
        errors = this.controlDays();

        if (errors != null && errors.length > 0) {
          this.onSelectionError();
          this.date = this.utility.toDate(moment());
        }
      }

      this.disablePastDays(days);
      this.refreshSelection(days);

      if (!this.startDate && !this.dateClicked) {
        this.checkDisabledButtons();
      }

      if (!this.controllingDisponibility) {
        this.setIsLoading(false);
      }
    });
  }

  setCssClass(cssClass: string, cssClassToAdd: string): string {
    if (!cssClass) {
      cssClass = cssClassToAdd;
    } else if (!cssClass.includes(cssClassToAdd)) {
      cssClass += ' ' + cssClassToAdd;
    }

    return cssClass;
  }

  onPaxChanged(camere: BookingSolutionRoomRQ[]) {
    this.showSearchButton();
    this.rq.camere = camere;
    this.refresh.next();
    this.getPrezzoMinimo();

    if (!!this.arrivoUrl && !!this.partenzaUrl) {
      setTimeout(() => {
        this.controllaDisponibilita();
      }, 200);
    }
  }

  controllaDisponibilita() {
    if (this.rq.arrivo != null && this.rq.partenza != null) {
      this.searchButtonVisible = false;
      this.daysSelected.emit(this.rq);
    } else {
      this.showError('booking.selezionaPeriodo');
    }
  }

  addFrom() {
    this.urlSearchParametersReceived = false;

    if (!!this.rq.arrivo) {
      this.rq.arrivo = this.getDate(moment(this.rq.arrivo).add(1, 'day'));

      if (this.dayUse) {
        this.rq.partenza = this.rq.arrivo;
      }

      this.getPrezzoMinimo();
      this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);
      this.resetSelection();
      this.refreshSelection(this.days);
      this.checkDisabledButtons();
      this.showSearchButton();
    }
  }

  minusFrom() {
    this.urlSearchParametersReceived = false;

    if (!!this.rq.arrivo) {
      this.rq.arrivo = this.getDate(moment(this.rq.arrivo).subtract(1, 'day'));

      if (this.dayUse) {
        this.rq.partenza = this.rq.arrivo;
      }

      this.getPrezzoMinimo();
      this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);
      this.resetSelection();
      this.refreshSelection(this.days);
      this.checkDisabledButtons();
      this.showSearchButton();
    }
  }

  addTo() {
    this.urlSearchParametersReceived = false;

    if (!!this.rq.partenza) {
      this.rq.partenza = this.getDate(moment(this.rq.partenza).add(1, 'day'));
      this.getPrezzoMinimo();
      this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);
      this.resetSelection();
      this.refreshSelection(this.days);
      this.checkDisabledButtons();
      this.showSearchButton();
    }
  }

  minusTo() {
    this.urlSearchParametersReceived = false;

    if (!!this.rq.partenza) {
      this.rq.partenza = this.getDate(moment(this.rq.partenza).subtract(1, 'day'));
      this.getPrezzoMinimo();
      this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);
      this.resetSelection();
      this.refreshSelection(this.days);
      this.checkDisabledButtons();
      this.showSearchButton();
    }
  }

  checkDisabledAddFrom(): boolean {
    if (!!this.rq.arrivo) {

      if (!this.dayUse) {
        if (this.nights <= 1) {
          this.addFromButtonDisabled = true;
          return true;
        }
      }

      if (this.isVisualizzaDisponibilitaCalendario) {
        if (this.dayUse) {
          let newDate = moment(this.rq.arrivo).add(1, 'day');

          let date = this.days.find(x => this.getDate(x.date) == this.getDate(newDate));

          if (date == null) {
            this.addFromButtonDisabled = true;
            return true;
          }

          if (date.cssClass != null && date.cssClass.includes('not-available')) {
            this.addFromButtonDisabled = true;
            return true;
          }

          if (this.numeroMinimoGiorniSelectedDays > 1) {
            this.addFromButtonDisabled = true;
            return true;
          }
        } else {
          if (this.numeroMinimoGiorniSelectedDays > 0) {
            if (this.nights - 1 < this.numeroMinimoGiorniSelectedDays) {
              this.addFromButtonDisabled = true;
              return true;
            }
          }
        }
      }

      this.addFromButtonDisabled = false;
      return false;
    }

    this.addFromButtonDisabled = true;
    return true;
  }

  checkDisabledMinusFrom(): boolean {
    if (!!this.rq.arrivo) {
      let newDate = moment(this.rq.arrivo).subtract(1, 'day');

      if (newDate < moment().startOf('day')) {
        this.minusFromButtonDisabled = true;
        return true;
      }

      if (this.isVisualizzaDisponibilitaCalendario) {
        let date = this.days.find(x => this.getDate(x.date) == this.getDate(newDate));

        if (date == null) {
          this.minusFromButtonDisabled = true;
          return true;
        }

        if (date.cssClass != null && date.cssClass.includes('not-available')) {
          this.minusFromButtonDisabled = true;
          return true;
        }

        if (!this.dayUse) {
          if (this.numeroMassimoGiorniSelectedDays > 0) {
            if (this.nights + 1 > this.numeroMassimoGiorniSelectedDays) {
              this.minusFromButtonDisabled = true;
              return true;
            }
          }
        } else {
          if (this.numeroMinimoGiorniSelectedDays > 1) {
            this.minusFromButtonDisabled = true;
            return true;
          }
        }
      }

      this.minusFromButtonDisabled = false;
      return false;
    }

    this.minusFromButtonDisabled = true;
    return true;
  }

  checkDisabledAddTo(): boolean {
    if (!!this.rq.partenza) {
      if (this.isVisualizzaDisponibilitaCalendario) {
        let date = this.days.find(x => this.getDate(x.date) == this.getDate(moment(this.rq.partenza).add(1, 'day')));

        if (date == null) {
          this.addToButtonDisabled = true;
          return true;
        }

        let partenza = this.days.find(x => this.getDate(x.date) == this.getDate(this.rq.partenza));

        if (partenza == null || (partenza.cssClass != null && partenza.cssClass.includes('not-available'))) {
          this.addToButtonDisabled = true;
          return true;
        }

        if (this.numeroMassimoGiorniSelectedDays > 0) {
          if (this.nights + 1 > this.numeroMassimoGiorniSelectedDays) {
            this.addToButtonDisabled = true;
            return true;
          }
        }
      }

      this.addToButtonDisabled = false;
      return false;
    }

    this.addToButtonDisabled = true;
    return true;
  }

  checkDisabledMinusTo(): boolean {
    if (!!this.rq.partenza) {
      if (this.nights <= 1) {
        this.minusToButtonDisabled = true;
        return true;
      }

      if (this.isVisualizzaDisponibilitaCalendario) {
        if (this.numeroMinimoGiorniSelectedDays > 0) {
          if (this.nights - 1 < this.numeroMinimoGiorniSelectedDays) {
            this.minusToButtonDisabled = true;
            return true;
          }
        }
      }

      this.minusToButtonDisabled = false;
      return false;
    }

    this.minusToButtonDisabled = true;
    return true;
  }

  checkDisabledButtons() {
    this.daysControlReset();
    this.controlLimitations();
    this.checkDisabledAddFrom();
    this.checkDisabledAddTo();
    this.checkDisabledMinusFrom();
    this.checkDisabledMinusTo();
  }

  controlLimitations() {
    for (let d = this.rq.arrivo; moment(d) <= moment(this.rq.partenza); d = this.getDate(moment(d).add(1, 'day'))) {
      let index = this.days.findIndex(x => this.getDate(x.date) == d);

      if (index >= 0) {
        if (!!this.days[index].cssClass) {
          if (this.days[index].cssClass.includes('limitations')) {
            if (!this.isControllaDurateSoggiornoSulGiornoDiArrivo || d == this.rq.arrivo) {
              let disp = this.disponibilita.find(x => this.getDate(x.data) == d);
              this.numeroMinimoGiorniSelectedDays = Math.max(this.numeroMinimoGiorniSelectedDays, disp.durataMinimaSoggiorno);

              if (this.numeroMassimoGiorniSelectedDays == null) {
                this.numeroMassimoGiorniSelectedDays = disp.durataMassimaSoggiorno;
              } else {
                this.numeroMassimoGiorniSelectedDays = Math.min(this.numeroMassimoGiorniSelectedDays, disp.durataMassimaSoggiorno);
              }
            }
          }
        }
      }
    }
  }

  getPrezzoMinimo(arrivo?: string, partenza?: string) {
    if ((!!this.rq.arrivo && !!this.rq.partenza) || (!!arrivo && !!partenza)) {
      this.prezzoCalculated = false;

      if (this.prezzoMinimoRequest != null) {
        this.prezzoMinimoRequest.unsubscribe();
      }

      let rq = new GetPrezzoMinimoRQ(this.adultiDefault);

      if (!!this.rq.arrivo && !!this.rq.partenza) {
        rq.arrivo = this.rq.arrivo;
        rq.partenza = this.rq.partenza;
      } else {
        rq.arrivo = arrivo;
        rq.partenza = partenza;
      }

      rq.oidMotivoViaggio = this.rq.oidMotivoViaggio;
      rq.oidPortale = this.oidPortale;
      rq.isDayUse = this.dayUse;
      rq.camere = this.rq.camere;

      this.prezzoMinimo = null;

      this.prezzoMinimoRequest = this.apiService.getPrezzoMinimo(rq).subscribe((res: WebApiResponse) => {
        this.prezzoMinimo = res.data;
        this.prezzoCalculated = true;
      });
    }
  }

  addFromMobile() {
    this.urlSearchParametersReceived = false;

    if (!!this.rq.arrivo) {
      this.rq.arrivo = this.getDate(moment(this.rq.arrivo).add(1, 'day'));

      if (this.dayUse) {
        this.rq.partenza = this.rq.arrivo;
      }

      this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);
      this.checkDisabledButtonsMobile();
      this.showSearchButton();
    }
  }

  minusFromMobile() {
    this.urlSearchParametersReceived = false;

    if (!!this.rq.arrivo) {
      this.rq.arrivo = this.getDate(moment(this.rq.arrivo).subtract(1, 'day'));

      if (this.dayUse) {
        this.rq.partenza = this.rq.arrivo;
      }

      this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);
      this.checkDisabledButtonsMobile();
      this.showSearchButton();
    }
  }

  addToMobile() {
    this.urlSearchParametersReceived = false;

    if (!!this.rq.partenza) {
      this.rq.partenza = this.getDate(moment(this.rq.partenza).add(1, 'day'));
      this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);
      this.checkDisabledButtonsMobile();
      this.showSearchButton();
    }
  }

  minusToMobile() {
    this.urlSearchParametersReceived = false;

    if (!!this.rq.partenza) {
      this.rq.partenza = this.getDate(moment(this.rq.partenza).subtract(1, 'day'));
      this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);
      this.checkDisabledButtonsMobile();
      this.showSearchButton();
    }
  }

  checkDisabledAddFromMobile(): boolean {
    if (!!this.rq.arrivo) {
      if (!this.dayUse) {
        if (this.nights <= 1) {
          this.addFromButtonDisabled = true;
          return true;
        }
      }

      this.addFromButtonDisabled = false;
      return false;
    }

    this.addFromButtonDisabled = true;
    return true;
  }

  checkDisabledMinusFromMobile(): boolean {
    if (!!this.rq.arrivo) {
      let newDate = moment(this.rq.arrivo).subtract(1, 'day');

      if (newDate < moment().startOf('day')) {
        this.minusFromButtonDisabled = true;
        return true;
      }

      this.minusFromButtonDisabled = false;
      return false;
    }

    this.minusFromButtonDisabled = true;
    return true;
  }

  checkDisabledMinusToMobile(): boolean {
    if (!!this.rq.partenza) {
      let newDate = moment(this.rq.partenza).subtract(1, 'day');

      if (newDate < moment().startOf('day')) {
        this.minusToButtonDisabled = true;
        return true;
      }

      if (this.nights <= 1) {
        this.minusToButtonDisabled = true;
        return true;
      }

      this.minusToButtonDisabled = false;
      return false;
    }

    this.minusToButtonDisabled = true;
    return true;
  }

  checkDisabledButtonsMobile() {
    this.checkDisabledAddFromMobile();
    this.checkDisabledMinusFromMobile();
    this.checkDisabledMinusToMobile();
  }

  onArrivoChange(value: any) {
    this.urlSearchParametersReceived = false;
    this.rq.arrivo = this.getDate(value);

    if (!!this.rq.arrivo && moment(this.rq.arrivo) < moment().startOf('day')) {
      this.rq.arrivo = this.getDate(moment());
    }

    this.calcolaNotti();

    if (!this.dayUse) {
      if (this.nights <= 1) {
        this.rq.partenza = this.getDate(moment(this.rq.arrivo).add(1, 'day'));
      }
    } else {
      this.rq.partenza = this.rq.arrivo;
    }

    this.checkDisabledButtonsMobile();
    this.showSearchButton();

    setTimeout(() => {
      this.arrivoDate = null;
    }, 200);
  }

  onPartenzaChange(value: any) {
    this.urlSearchParametersReceived = false;
    this.rq.partenza = this.getDate(value);

    if (!!this.rq.partenza && moment(this.rq.partenza) < moment().add(1, 'day').startOf('day')) {
      this.rq.partenza = this.getDate(moment().add(1, 'day'));
    }

    this.calcolaNotti();

    if (this.nights <= 1) {
      this.rq.arrivo = this.getDate(moment(this.rq.partenza).subtract(1, 'day'));
    }

    this.checkDisabledButtonsMobile();
    this.showSearchButton();

    setTimeout(() => {
      this.partenzaDate = null;
    }, 200);
  }

  calcolaNotti() {
    if (!!this.rq.arrivo && !!this.rq.partenza) {
      this.nights = Math.max(moment(this.rq.partenza).diff(moment(this.rq.arrivo), 'days'), 1);
    } else {
      this.nights = null;
    }
  }

  showSearchButton() {
    let searchButtonVisibleOld = this.searchButtonVisible;

    this.searchButtonVisible = true;

    if (!searchButtonVisibleOld && this.rq.arrivo != null && this.rq.partenza != null) {
      this.blinkStatus = 'dark';
      this.blinkStatus = 'light';

      setTimeout(() => {
        this.blinkStatus = 'dark';
        this.blinkStatus = null;
      }, 500);
    }
  }

  sendRequest() {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= this.mobileBreakpoint) {
      dialogOptions.width = '80vw';
    } else {
      dialogOptions.width = '95vw';
    }

    dialogOptions.minHeight = '80vh';
    dialogOptions.maxHeight = '95vh';
    dialogOptions.panelClass = 'booking-dialog-no-padding';

    let ref = this.showModalDialog(RichiesteAddComponent, dialogOptions);
    let component = <RichiesteAddComponent>ref.componentInstance;
    component.dialogRef = ref;
    component.oidAlbergo = this.oidAlbergo;
    component.oidPortale = this.oidPortale;
    component.language = this.lingua;
    component.booking = true;

    if (this.sessionService.sessione != null && this.sessionService.sessione.clienteMySelf != null) {
      let cliente = new Cliente();
      cliente.nome = this.sessionService.sessione.clienteMySelf.nome;
      cliente.cognome = this.sessionService.sessione.clienteMySelf.cognome;
      cliente.email = this.sessionService.sessione.clienteMySelf.email;
      cliente.cellulare = this.sessionService.sessione.clienteMySelf.cellulare;
      component.cliente = cliente;
    }
  }

  openCodicePromozioneSelect() {
    this.setIsLoading(true);

    let rq = new GetCodiciPromozioneValidiRQ();
    rq.arrivo = this.rq.arrivo;
    rq.partenza = this.rq.partenza;
    rq.oidAlbergo = this.sessionService.getOidAlbergo();
    rq.oidPortale = this.oidPortale;
    rq.oidTipoCameraOTA = this.oidTipoCameraOTA;
    rq.oidTariffaOTA = this.oidTariffaOTA;

    this.apiService.getCodiciPromozioneValidi(rq).subscribe(rs => {
      this.setIsLoading(false);

      if (rs.data != null && rs.data.length > 0) {
        let dialogOptions = new DialogOptions();
        dialogOptions.width = '40vw';

        let ref = this.showModalDialog(ViewSelectDialogComponent, dialogOptions);
        let component = <ViewSelectDialogComponent>ref.componentInstance;

        component.dialogRef = ref;
        component.title = 'sbm.calcolaRetta.codicePromozione';
        component.data = rs.data;
        component.displayMember = 'codice';

        ref.afterClosed().subscribe((x: CodicePromozione) => {
          if (x) {
            this.rq.codicePromozione = x.codice;
          }
        });
      } else {
        this.showError('sbm.calcolaRetta.nessunCodicePromozioneValido');
      }
    });
  }

  nightsSelected(notti: number) {
    this.rq.arrivo = this.startDate;
    this.rq.partenza = this.getDate(moment(this.startDate).add(notti, 'days'));
    this.dateClicked = false;
    this.startDate = null;
    this.nights = notti;
    this.resetSelection();
    this.refreshSelection(this.days);
    this.showSearchButton();
    this.checkDisabledButtons();
    this.getPrezzoMinimo();
    this.showNightsSelect = false;
    this.controllaDisponibilita();
  }

  nightsChange() {
    this.setIsLoading(true);

    let getDisponibilitaNottiRQ = new GetDisponibilitaNottiRQ();
    getDisponibilitaNottiRQ.oidPortale = this.oidPortale;
    getDisponibilitaNottiRQ.arrivo = this.startDate;
    getDisponibilitaNottiRQ.notti = this.disponibilitaNotti[this.disponibilitaNotti.length - 1].notti;

    this.apiService.getDisponibilitaNotti(getDisponibilitaNottiRQ).subscribe(getDisponibilitaNottiRS => {
      if (getDisponibilitaNottiRS.data != null && getDisponibilitaNottiRS.data.length == 1) {
        this.disponibilitaNotti[this.disponibilitaNotti.length - 1] = getDisponibilitaNottiRS.data[0];
      }

      this.setIsLoading(false);
    });
  }

  nightsSelectionBack() {
    this.onSelectionError();
    this.notAvailableDate = null;
    this.showNightsSelect = false;
  }

  ngOnDestroy() {
    this.sub.unsubscribe();

    if (this.sub1 != null) {
      this.sub1.unsubscribe();
    }

    if (this.sub2 != null) {
      this.sub2.unsubscribe();
    }

    this.refresh.complete();
  }
}