import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { GoogleTagManagerService } from 'angular-google-tag-manager';
import * as moment from 'moment';
import { Subscription } from 'rxjs/internal/Subscription';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
  Albergo,
  Cliente,
  Lingua,
  Nazione,
  ParametroRicerca,
  PortaleXAlbergo,
  Richiesta,
  RichiestaCamere,
  RichiestaCamerePax,
} from 'src/app/shared/models/bo.model';
import {
  CheckAlbergoRS,
  CheckPortaleRS,
  CreaRichiestaRQ,
  CreaRichiestaRS,
  DialogOptions,
  GetFormV1ViewRQ,
  GetFormV1ViewRS,
  GetFormV1ViewRSMotivoViaggio,
  GetFormV1ViewRSTariffa,
  GetFormV1ViewRSTipoCamera,
  GetPrivacyPortaleRQ,
  NewsLetterPortale,
  PrivacyPortale,
  WebApiRequestFilter,
  WebApiResponseFilter,
} from 'src/app/shared/models/dto';
import {
  CampoObbligatorioFormRichiesta,
  CriteriaOperator,
  FilterValueType,
  Pagina,
  TipoComponentePrenotazione,
  TipoRichiestaPreventivo,
} from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { CssLoaderService } from 'src/app/shared/services/css-loader.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { ClienteSelectComponent } from '../cliente-select/cliente-select.component';
import { PrivacyNewsletterDialogComponent } from './privacy-newsletter-dialog/privacy-newsletter-dialog.component';

@Component({
  selector: 'app-form-v1',
  templateUrl: './form-v1.component.html',
  styleUrls: ['./form-v1.component.scss']
})
export class FormV1Component extends BaseComponent implements OnInit, OnDestroy {

  @Input() dialogRef: MatDialogRef<{}, any>;
  @Input() createPreventivo: boolean = false;
  @Input() modal: boolean = false;
  @Input() oidAlbergo: number;
  @Input() oidPortale: number;
  @Input() language: string;
  @Input() cliente: Cliente;
  @Input() booking: boolean = false;

  @Output() onLoaded: EventEmitter<any> = new EventEmitter();
  @Output() onRichiestaSaved: EventEmitter<number> = new EventEmitter();
  @Output() onPortaleSelected: EventEmitter<number> = new EventEmitter();

  public sub: Subscription;
  public model = new Richiesta();
  public lingua: string;
  public linguaObj: Lingua;
  public tipiCamera: GetFormV1ViewRSTipoCamera[] = [];
  public tariffe: GetFormV1ViewRSTariffa[] = [];
  public tipiCameraOTAPortale: GetFormV1ViewRSTipoCamera[] = [];
  public tariffeOTAPortale: GetFormV1ViewRSTariffa[] = [];
  public parametriRicercaMercato: ParametroRicerca[] = [];
  public parametriRicercaCanaleVendita: ParametroRicerca[] = [];
  public portali: PortaleXAlbergo[] = [];
  public portaliList: PortaleXAlbergo[] = [];
  public initialized: boolean = false;
  public oidPortaleReceived: boolean;
  public enumCampoObbligatorioFormRichiesta = CampoObbligatorioFormRichiesta;
  public cellulareRequired: boolean = false;
  public tipoRichiestaRequired: boolean = false;
  public mercatoRequired: boolean = false;
  public canaleVenditaRequired: boolean = false;
  public nazioneRequired: boolean = false;
  public noteRequired: boolean = false;
  public nomeRequired: boolean = false;
  public tipoCameraRequired: boolean = false;
  public tariffaRequired: boolean = false;
  public motivoViaggioRequired: boolean = false;
  public enumTipoRichiesta = TipoRichiestaPreventivo;
  public camerePrenotabili: number = 10;
  public etaMinimaBambini: number = 0;
  public etaMassimaBambini: number = 17;
  public adultiDefault: number = 2;
  public adultiMax: number = 0;
  public bambiniMax: number = 0;
  public isVisualizzaTipoCamera: boolean = true;
  public isVisualizzaTrattamento: boolean = true;
  public isDisattivaRichiestaBambini: boolean = false;
  public motiviViaggio: GetFormV1ViewRSMotivoViaggio[] = [];
  public banner: string;
  public coloreBanner: string;
  public gtmService: GoogleTagManagerService;
  public data: string;
  public portale: PortaleXAlbergo;
  public minDate: string;
  public newsLetterPortale: NewsLetterPortale;

  constructor(private route: ActivatedRoute,
    private router: Router,
    public apiService: ApiService,
    private localizationService: LocalizationService,
    private cssLoaderService: CssLoaderService,
    private containerService: ContainerService,
    public utility: UtilityService) {
    super();
  }

  ngOnInit() {
    if (!this.modal) {
      this.containerService.setScrollingContainer('richiesta');
    }

    if (this.cliente != null) {
      this.model.cliente = this.cliente;
    }

    if (this.model.cliente.mercato == null) {
      this.model.cliente.mercato = new ParametroRicerca();
    }

    if (this.model.cliente.canaleVendita == null) {
      this.model.cliente.canaleVendita = new ParametroRicerca();
    }

    if (this.model.cliente.lingua == null) {
      this.model.cliente.lingua = new Lingua();
    }

    this.model.cliente.nazioneObject = new Nazione();

    if (!!this.model.cliente.nazione) {
      let rq = WebApiRequestFilter.fromFilter('nomeItaliano', CriteriaOperator.Equal, this.model.cliente.nazione, FilterValueType.String);

      this.apiService.getNazioniFilter(rq).subscribe((res: WebApiResponseFilter) => {
        if (res.data.length > 0) {
          this.model.cliente.nazioneObject = res.data[0];
        }
      });
    }

    if (this.booking) {
      this.model.urlRichiesta = window.location.href;
      this.model.urlReferrer = document.referrer;
    } else {
      this.model.dataVisualizzazione = this.getDate(moment());
    }

    this.model.isDayUse = false;

    this.oidPortaleReceived = this.oidPortale != null;

    if (this.oidAlbergo == null) {
      this.sub = this.route.queryParamMap.subscribe(x => {
        let oidPortale = x.get('oidPortale');
        let oidAlbergo = x.get('oidAlbergo');

        this.model.isDayUse = x.get('dayUse') == 'true';

        if ((!!oidPortale && !isNaN(+oidPortale)) || (!!oidAlbergo && !isNaN(+oidAlbergo))) {
          this.setIsLoading(true);

          if (!!oidPortale && !isNaN(+oidPortale)) {
            this.apiService.checkPortale(+oidPortale).subscribe((res: CheckPortaleRS) => {
              if (res.data.success && res.data.oidAlbergo > 0) {
                this.oidPortale = +oidPortale;
                this.oidAlbergo = +res.data.oidAlbergo;
                this.cssLoaderService.load(this.oidAlbergo, Pagina.Richiesta, res.data.oidUbicazioneIstat);

                this.oidPortaleReceived = true;

                this.init(x.get('lingua'), true);
              } else {
                this.router.navigate(['sbm']).then(() => {
                  this.setIsLoading(false);
                });
              }
            });
          } else {
            this.apiService.checkAlbergo(+oidAlbergo).subscribe((res: CheckAlbergoRS) => {
              if (res.data.success) {
                this.oidAlbergo = +oidAlbergo;
                this.cssLoaderService.load(this.oidAlbergo, Pagina.Richiesta, res.data.oidUbicazioneIstat);

                this.oidPortaleReceived = false;

                this.init(x.get('lingua'), true);
              } else {
                this.router.navigate(['sbm']).then(() => {
                  this.setIsLoading(false);
                });
              }
            });
          }
        } else {
          this.router.navigate(['sbm']);
        }
      });
    } else {
      this.setIsLoading(true);
      this.init(this.language);
    }
  }

  init(lingua: string, setTranslation: boolean = false) {
    this.utility.setLingua(lingua).then((lingua: Lingua) => {
      this.lingua = lingua?.codice;
      this.linguaObj = lingua;

      this.model.cliente.codiceLingua = this.lingua;

      let rq = new GetFormV1ViewRQ();
      rq.oidAlbergo = this.oidAlbergo;
      rq.oidPortale = this.oidPortale;
      rq.lingua = this.lingua;

      this.apiService.getViewFormV1(rq).subscribe((res: GetFormV1ViewRS) => {
        this.tipiCamera = res.data.tipiCamera;
        this.tariffe = res.data.tariffe;
        this.parametriRicercaMercato = res.data.parametriRicercaMercato;
        this.parametriRicercaCanaleVendita = res.data.parametriRicercaCanaleVendita;
        this.motiviViaggio = res.data.motiviViaggio;
        this.newsLetterPortale = res.data.newsLetterPortale;

        if (setTranslation && !!res.data.traduzioni) {
          let json;

          try {
            json = JSON.parse(res.data.traduzioni);
          } catch (e) { }

          if (!!json) {
            this.localizationService.setTranslation(this.localizationService.getCurrentLocale(), json);
          }
        }

        this.portali = res.data.portali;
        this.portaliList = this.portali.filter(x => x.isVisualizzaRichiesteCrm);

        if (this.portaliList.length > 0 && this.oidPortale == null) {
          this.oidPortale = this.portaliList[0].oid;
        }

        this.onPortaleChange();

        this.onCameraAdd();
        this.initialized = true;
        this.setIsLoading(false);
        this.onLoaded.emit();
      });
    });
  }

  onNumeroBambiniChange(cameraIndex: number, add: boolean, numero: number) {
    if (add) {
      let bambino = new RichiestaCamerePax();
      bambino.tipo = TipoComponentePrenotazione.Bambino;
      bambino.anni = this.etaMinimaBambini;
      bambino.quantita = 1;
      this.model.camere[cameraIndex].pax.push(bambino);
    } else {
      this.model.camere[cameraIndex].pax.pop();
    }

    this.model.camere[cameraIndex].numeroBambini = numero;
  }

  onNumeroAdultiChange(cameraIndex: number, numero: number) {
    this.model.camere[cameraIndex].pax[0].quantita = numero;
    this.model.camere[cameraIndex].numeroAdulti = numero;
  }

  onCameraAdd() {
    if (this.model.camere.length < this.camerePrenotabili) {
      let camera = new RichiestaCamere();
      camera.numeroAdulti = this.adultiDefault;
      camera.numeroBambini = 0;
      let pax = new RichiestaCamerePax();
      pax.tipo = TipoComponentePrenotazione.Adulto;
      pax.anni = -1;
      pax.quantita = this.adultiDefault;
      camera.pax = [pax];
      this.model.camere.push(camera);
    }
  }

  onCameraRemove(index: number) {
    this.model.camere.splice(index, 1);
  }

  save() {
    if (this.model.isDayUse) {
      this.model.dal = this.data;
      this.model.al = this.data;
    }

    if (!!this.model.dal && !!this.model.al && this.model.dal <= this.model.al && !!this.model.cliente.cognome && (this.model.tipoRichiesta == TipoRichiestaPreventivo.Telefono || this.model.tipoRichiesta == TipoRichiestaPreventivo.WhatsApp || !!this.model.cliente.email)) {
      if (((!this.cellulareRequired && this.model.tipoRichiesta != TipoRichiestaPreventivo.WhatsApp) || (!!this.model.cliente.cellulare && this.model.cliente.cellulare.length >= 8)) && (!this.tipoRichiestaRequired || this.model.tipoRichiesta != null) && (!this.mercatoRequired || (this.model.cliente.mercato != null && this.model.cliente.mercato.oid > 0)) && (!this.canaleVenditaRequired || (this.model.cliente.canaleVendita != null && this.model.cliente.canaleVendita.oid > 0)) && (!this.nazioneRequired || (this.model.cliente.nazioneObject != null && this.model.cliente.nazioneObject.oid > 0)) && (!this.noteRequired || !!this.model.note) && (!this.tipoCameraRequired || this.model.camere.every(x => x.tipoCameraOTA != null && x.tipoCameraOTA.oid > 0)) && (!this.tariffaRequired || this.model.camere.every(x => x.tariffaOTA != null && x.tariffaOTA.oid > 0)) && (!this.motivoViaggioRequired || (this.model.motivoViaggio != null && this.model.motivoViaggio.oid > 0)) && (!this.nomeRequired || !!this.model.cliente.nome)) {
        if ((!!this.model.cliente.email && this.utility.validateEmail(this.model.cliente.email)) || (!this.model.cliente.email && (this.model.tipoRichiesta == TipoRichiestaPreventivo.Telefono || this.model.tipoRichiesta == TipoRichiestaPreventivo.WhatsApp))) {
          if (this.oidAlbergo != null) {
            this.model.albergo = new Albergo(this.oidAlbergo);
          }

          if (this.oidPortale != null) {
            this.model.portaleXAlbergo = new PortaleXAlbergo(this.oidPortale);
          }

          this.model.dal = this.getDate(this.model.dal);
          this.model.al = this.getDate(this.model.al);

          let richiestaModel: Richiesta = JSON.parse(JSON.stringify(this.model));
          delete richiestaModel.richiestaJSON;

          for (let camera of richiestaModel.camere) {
            delete camera.numeroAdulti;
            delete camera.numeroBambini;
            delete camera.richiesta;

            for (let pax of camera.pax) {
              delete pax.richiestaCamere;
            }
          }

          this.model.richiestaJSON = JSON.stringify(richiestaModel);

          this.setIsLoading(true);

          let rq = new CreaRichiestaRQ();
          rq.richiesta = this.model;
          rq.isInviaEmail = this.booking;

          this.apiService.creaRichiesta(rq).subscribe((res: CreaRichiestaRS) => {
            if (res.data != null) {
              this.model.oid = res.data.oidRichiesta;

              if (this.gtmService != null && this.portale != null && this.portale.albergo != null) {
                this.gtmService.pushTag({
                  event: 'new_booking_form_richiesta',
                  booking: {
                    hotelId: this.portale.albergo.oid,
                    hotelName: this.portale.albergo.nome,
                    portalId: this.oidPortale,
                    portalNome: this.portale.nome,
                    rooms: this.model.camere.length,
                    roomNights: Math.max(moment(this.model.al).diff(moment(this.model.dal), 'days'), 1),
                    checkInDate: this.model.dal,
                    checkOutDate: this.model.al,
                    lingua: this.lingua
                  }
                });
              }

              if (!this.createPreventivo) {
                this.setIsLoading(false);

                this.showInfo('interfaces.form-v1.richiestaInviata').then(() => {
                  if (this.dialogRef != null) {
                    this.dialogRef.close(true);
                  } else {
                    window.location.reload();
                  }
                });
              } else {
                this.onRichiestaSaved.emit(this.model.oid);
              }
            }
          });
        } else {
          this.showError('globals.messages.emailNonValida');
        }
      } else {
        this.showError('globals.messages.inserireDati');
      }
    } else {
      this.showError('globals.messages.inserireDati');
    }
  }

  onArrivoChange() {
    setTimeout(() => {
      this.model.dal = this.getDate(this.model.dal);

      if (moment(this.model.dal) < moment().startOf('day')) {
        this.model.dal = this.getDate(moment());
      }

      if (this.model.al == null || moment(this.model.dal) >= moment(this.model.al)) {
        this.model.al = this.getDate(moment(this.model.dal).add(1, 'day'));
      }
    }, 200);
  }

  onPartenzaChange() {
    setTimeout(() => {
      this.model.al = this.getDate(this.model.al);

      if (moment(this.model.al) < moment().add(1, 'day').startOf('day')) {
        this.model.al = this.getDate(moment().add(1, 'day'));
      }

      if (this.model.dal == null || moment(this.model.dal) >= moment(this.model.al)) {
        this.model.dal = this.getDate(moment(this.model.al).subtract(1, 'day'));
      }
    }, 200);
  }

  onDataChange() {
    setTimeout(() => {
      this.data = this.getDate(this.data);

      if (moment(this.data) < moment().startOf('day')) {
        this.data = this.getDate(moment());
      }
    }, 200);
  }

  onPortaleChange() {
    for (let camera of this.model.camere) {
      camera.tipoCameraOTA.oid = -1;
      camera.tariffaOTA.oid = -1;
      camera.nomeTipoCamera = '';
      camera.oidTariffe = [];
    }

    this.tipiCameraOTAPortale = this.tipiCamera.filter(x => x.portaleXAlbergo.oid == this.oidPortale && this.model.isDayUse == x.isDayUse);

    if (this.tipiCameraOTAPortale.length > 0) {
      let tipoCamera = new GetFormV1ViewRSTipoCamera();
      tipoCamera.oid = -1;
      tipoCamera.nome = '';
      this.tipiCameraOTAPortale.unshift(tipoCamera);
    }

    this.tariffeOTAPortale = this.tariffe.filter(x => x.portaleXAlbergo.oid == this.oidPortale && this.model.isDayUse == x.isDayUse);

    this.cellulareRequired = false;
    this.tipoRichiestaRequired = false;
    this.mercatoRequired = false;
    this.canaleVenditaRequired = false;
    this.nazioneRequired = false;
    this.noteRequired = false;
    this.nomeRequired = false;
    this.tipoCameraRequired = false;
    this.tariffaRequired = false;
    this.motivoViaggioRequired = false;

    if (this.oidPortale != null) {
      this.portale = this.portali.find(x => x.oid == this.oidPortale);

      if (this.portale != null) {
        let camerePrenotabili = this.booking ? this.portale.camerePrenotabili : this.portale.camerePrenotabiliCrm;
        this.camerePrenotabili = camerePrenotabili <= 0 ? 10 : Math.min(camerePrenotabili, 10);
        this.etaMinimaBambini = this.portale.etaMinimaBambini > 0 && this.portale.etaMinimaBambini <= 17 ? this.portale.etaMinimaBambini : 0;
        this.etaMassimaBambini = this.portale.etaMassimaBambini > 0 && this.portale.etaMassimaBambini <= 17 ? this.portale.etaMassimaBambini : 17;
        this.adultiDefault = this.portale.adultiDefaultInPrenotazione <= 0 ? 1 : this.portale.adultiDefaultInPrenotazione;
        this.adultiMax = Math.max(this.portale.numeroMassimoAdulti, 0);
        this.bambiniMax = Math.max(this.portale.numeroMassimoRiduzioni, 0);

        let minDate = moment().startOf('day');

        if (!!this.portale.dataInizioGestione) {
          let dataInizioGestione = moment(this.portale.dataInizioGestione).startOf('day');

          if (dataInizioGestione.isAfter(minDate)) {
            minDate = dataInizioGestione;
          }
        }

        this.minDate = this.getDate(minDate);

        let campiObbligatori: CampoObbligatorioFormRichiesta[] = [];

        if (this.createPreventivo) {
          if (!!this.portale.campiObbligatoriPreventivo) {
            let campi = this.portale.campiObbligatoriPreventivo.split(';');

            for (let campo of campi) {
              if (campo != '' && !isNaN(+campo) && +campo in CampoObbligatorioFormRichiesta) {
                campiObbligatori.push(+campo as CampoObbligatorioFormRichiesta);
              }
            }

            if (campiObbligatori != null && campiObbligatori.length > 0) {
              this.cellulareRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.Cellulare) >= 0;
              this.tipoRichiestaRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.TipoRichiesta) >= 0;
              this.mercatoRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.Mercato) >= 0;
              this.canaleVenditaRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.CanaleVendita) >= 0;
              this.nazioneRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.Nazione) >= 0;
              this.motivoViaggioRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.MotivoViaggio) >= 0 && this.motiviViaggio != null && this.motiviViaggio.length > 0;
              this.nomeRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.Nome) >= 0;
            }
          }
        } else if (this.booking) {
          this.isDisattivaRichiestaBambini = this.portale.isDisattivaRichiestaBambiniFormRichiesta;
          this.banner = this.portale.bannerFormRichiesta;
          this.coloreBanner = this.portale.coloreBannerFormRichiesta;

          //DA CONTROLLARE
          //if (!this.modal) {
          this.isVisualizzaTipoCamera = this.portale.isVisualizzaTipoCameraFormRichiesta;
          this.isVisualizzaTrattamento = this.portale.isVisualizzaTrattamentoFormRichiesta;

          if (!!this.portale.campiObbligatoriFormRichiesta) {
            let campi = this.portale.campiObbligatoriFormRichiesta.split(';');

            for (let campo of campi) {
              if (campo != '' && !isNaN(+campo) && +campo in CampoObbligatorioFormRichiesta) {
                campiObbligatori.push(+campo as CampoObbligatorioFormRichiesta);
              }
            }

            if (campiObbligatori != null && campiObbligatori.length > 0) {
              this.cellulareRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.Cellulare) >= 0;
              this.noteRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.Note) >= 0;
              this.tipoCameraRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.TipoCamera) >= 0 && this.isVisualizzaTipoCamera && this.tipiCameraOTAPortale.length > 0;
              this.tariffaRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.Tariffa) >= 0 && this.isVisualizzaTrattamento && this.tariffeOTAPortale.length > 0;
              this.motivoViaggioRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.MotivoViaggio) >= 0 && this.motiviViaggio != null && this.motiviViaggio.length > 0;
              this.nomeRequired = campiObbligatori.indexOf(CampoObbligatorioFormRichiesta.Nome) >= 0;
            }
          }
          //}

          if (!this.modal && this.oidPortaleReceived && this.gtmService == null && !!this.portale.googleGTM) {
            this.gtmService = new GoogleTagManagerService(null, this.portale.googleGTM, null, null, null, null, null);

            this.gtmService.pushTag({
              event: 'page',
              pageName: 'form-richiesta'
            });
          }
        }

        if (!!this.portale.dataInizioGestione && moment(this.getDate(this.portale.dataInizioGestione)).isAfter(moment().startOf('day'))) {
          if (this.model.isDayUse) {
            this.data = this.portale.dataInizioGestione;
          } else {
            this.model.dal = this.portale.dataInizioGestione;
          }
        }

        if (!this.booking && this.portale.tipoRichiestaDefault != null) {
          this.model.tipoRichiesta = this.portale.tipoRichiestaDefault;
        }
      }
    }

    this.onPortaleSelected.emit(this.oidPortale);
  }

  openClienteSelect() {
    let c: Cliente;

    if (this.model.cliente != null && this.model.cliente.oid > 0) {
      c = Object.assign({}, this.model.cliente);
    } else {
      c = new Cliente(-1);
    }

    let dialogOptions = new DialogOptions();
    dialogOptions.width = '50vw';

    let ref = this.showModalDialog(ClienteSelectComponent, dialogOptions);
    let component = <ClienteSelectComponent>ref.componentInstance;

    component.cliente = c;
    component.dialogRef = ref;
    component.linguaDefault = this.linguaObj;

    ref.afterClosed().subscribe((cliente: Cliente) => {
      if (cliente != null && cliente.oid > 0) {
        this.model.cliente = cliente;

        if (this.model.cliente.mercato == null) {
          this.model.cliente.mercato = new ParametroRicerca();
        }

        if (this.model.cliente.canaleVendita == null) {
          this.model.cliente.canaleVendita = new ParametroRicerca();
        }

        if (this.model.cliente.lingua == null) {
          this.model.cliente.lingua = new Lingua();
        }

        this.model.cliente.nazioneObject = new Nazione();

        if (!!this.model.cliente.nazione) {
          let rq = WebApiRequestFilter.fromFilter('nomeItaliano', CriteriaOperator.Equal, this.model.cliente.nazione, FilterValueType.String);

          this.apiService.getNazioniFilter(rq).subscribe((res: WebApiResponseFilter) => {
            if (res.data.length > 0) {
              this.model.cliente.nazioneObject = res.data[0];
            }
          });
        }
      }
    });
  }

  openPrivacyDialog() {
    this.setIsLoading(true);

    let rq = new GetPrivacyPortaleRQ();
    rq.oidPortale = this.oidPortale;
    rq.lingua = this.lingua;

    this.apiService.getPrivacyPortale(rq).subscribe(rs => {
      this.setIsLoading(false);
      this.openPrivacyNewsletterDialog(rs.data);
    });
  }

  openNewsletterDialog() {
    this.openPrivacyNewsletterDialog(this.newsLetterPortale);
  }

  openPrivacyNewsletterDialog(privacyNewsletterPortale: PrivacyPortale | NewsLetterPortale) {
    if (!!privacyNewsletterPortale.html) {
      let dialogOptions = new DialogOptions();

      if (this.containerService.dimension.width >= 992) {
        dialogOptions.width = "50vw";
      } else {
        dialogOptions.width = "80vw";
      }

      let ref = this.showModalDialog(PrivacyNewsletterDialogComponent, dialogOptions);
      let component = <PrivacyNewsletterDialogComponent>ref.componentInstance;

      component.text = privacyNewsletterPortale.html;
    } else if (!!privacyNewsletterPortale.url) {
      this.utility.openUrlInNewTab(privacyNewsletterPortale.url);
    }
  }

  onTipoCameraChange(camera: RichiestaCamere, tipoCamera: GetFormV1ViewRSTipoCamera) {
    if (tipoCamera.oid > 0) {
      camera.tipoCameraOTA.oid = tipoCamera.oid;
      camera.nomeTipoCamera = tipoCamera.nome;
      camera.oidTariffe = tipoCamera.oidTariffe;

      if (camera.oidTariffe != null && camera.oidTariffe.length > 0 && camera.tariffaOTA.oid > 0 && !camera.oidTariffe.includes(camera.tariffaOTA.oid)) {
        camera.tariffaOTA.oid = -1;
      }
    } else {
      camera.tipoCameraOTA.oid = -1;
      camera.nomeTipoCamera = '';
      camera.oidTariffe = [];
    }
  }

  ngOnDestroy() {
    if (this.sub != null) {
      this.sub.unsubscribe();
    }
  }
}