import { AfterViewInit, Directive, ElementRef, HostListener, Input } from '@angular/core';

@Directive({
  selector: '[appRectangle]'
})
export class RectangleDirective implements AfterViewInit {

  @Input() appRectangleActive: boolean = true;
  @Input() heightMultiplier: number;
  @Input() setLineHeight: boolean;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.updateDivSize();
  }

  constructor(public el: ElementRef) { }

  ngAfterViewInit() {
    setTimeout(() => {
      this.updateDivSize();
    }, 200);
  }

  updateDivSize() {
    if (this.appRectangleActive) {
      let height: string;

      if (this.heightMultiplier == null || this.heightMultiplier <= 0) {
        height = Math.floor(this.el.nativeElement.offsetWidth) + 'px';
      } else {
        height = Math.floor(this.el.nativeElement.offsetWidth * this.heightMultiplier) + 'px';
      }

      this.el.nativeElement.style.height = height;
      this.el.nativeElement.style.minHeight = height;
      this.el.nativeElement.style.maxHeight = height;

      if (this.setLineHeight) {
        this.el.nativeElement.style.lineHeight = height;
      }
    }
  }
}