import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import {
  BookingReductionConfigRS,
  BookingSolutionPaxRQ,
  BookingSolutionRoomRQ,
  CameraBookingUrlParameter,
} from 'src/app/shared/models/dto';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-booking-room-select',
  templateUrl: './booking-room-select.component.html',
  styleUrls: ['./booking-room-select.component.scss']
})
export class BookingRoomSelectComponent implements OnInit {

  @Input() adultiDefault: number;
  @Input() adultiMax: number;
  @Input() bambiniMax: number;
  @Input() etaMinimaBambini: number;
  @Input() etaMassimaBambini: number;
  @Input() camereUrl: CameraBookingUrlParameter[];
  @Input() isEtaRiduzioniAttiva: boolean;
  @Input() riduzioni: BookingReductionConfigRS[];
  @Input() isDisattivaRichiestaBambini: boolean;
  @Input() dayUse: boolean;

  @Input() set _camerePrenotabili(value: number) {
    if (value != null) {
      this.camerePrenotabili = value <= 0 ? 10 : Math.min(value, 10);
    }
  };

  @Output() paxChanged: EventEmitter<BookingSolutionRoomRQ[]> = new EventEmitter();

  public camere: BookingSolutionRoomRQ[] = [];
  public bambini: number[][] = [];
  public camerePrenotabili: number = 10;

  constructor(public utilityService: UtilityService) { }

  ngOnInit() {
    let valid: boolean = false;

    if (this.camereUrl != null && this.camereUrl.length > 0) {
      this.camereUrl = this.camereUrl.slice(0, this.camerePrenotabili);
      
      for (let camera of this.camereUrl) {
        if (camera.adulti > 0) {
          camera.adulti = Math.min(camera.adulti, this.adultiMax > 0 ? Math.min(this.adultiMax, 12) : 12);

          valid = true;

          let room = new BookingSolutionRoomRQ(camera.adulti);

          if (camera.bambini != null && camera.bambini.length > 0) {
            let n = 0;

            for (let bambino of camera.bambini) {
              if (bambino.quantita > 0 && bambino.eta >= 0) {
                for (let i = 0; i < bambino.quantita; i++) {
                  if ((this.bambiniMax > 0 && n + 1 > this.bambiniMax) || n + 1 + camera.adulti > 12) {
                    break;
                  } else {
                    let b = new BookingSolutionPaxRQ();
                    b.quantita = 1;            
                    b.eta = Math.max(bambino.eta, this.etaMinimaBambini);
                    b.eta = Math.min(bambino.eta, this.etaMassimaBambini);
                    room.pax.push(b);
                    n++;
                  }
                }
              }
            }

            let bambini: number[] = [];

            for (let i = 1, index = 1; index <= n; i++) {
              if (i % 4 > 0) {
                bambini.push(index);
                index++;
              } else {
                bambini.push(-1);
              }
            }

            this.bambini.push(bambini);
          }

          this.camere.push(room);
        }
      }
    }

    if (valid) {
      this.isEtaRiduzioniAttiva = true;
      this.onCamereChange();
    } else {
      this.onCameraAdd();
    }
  }

  onNumeroBambiniChange(cameraIndex: number, add: boolean, numero: number) {
    let bambini: number[] = [];

    let bambiniCol = this.isEtaRiduzioniAttiva ? 4 : 2;

    for (let i = 1, index = 1; index <= numero; i++) {
      if (i % bambiniCol > 0) {
        bambini.push(index);
        index++;
      } else {
        bambini.push(-1);
      }
    }

    if (add) {
      let bambino = new BookingSolutionPaxRQ();
      bambino.eta = this.etaMinimaBambini;
      bambino.quantita = 1;

      if (!this.isEtaRiduzioniAttiva && this.riduzioni != null && this.riduzioni.length > 0) {
        bambino.oid = this.riduzioni[0].reductionId;
      }

      this.camere[cameraIndex].pax.push(bambino);
    } else {
      this.camere[cameraIndex].pax.pop();
    }

    this.bambini[cameraIndex] = bambini;

    this.onCamereChange();
  }

  onNumeroAdultiChange(cameraIndex: number, numero: number) {
    this.camere[cameraIndex].pax[0].quantita = numero;
    this.onCamereChange();
  }

  onCameraAdd() {
    if (this.camere.length < this.camerePrenotabili) {
      this.camere.push(new BookingSolutionRoomRQ(this.adultiDefault));
      this.bambini.push([]);
      this.onCamereChange();
    }
  }

  onCameraRemove(index: number) {
    this.camere.splice(index, 1);
    this.bambini.splice(index, 1);
    this.onCamereChange();
  }

  onCamereChange() {
    this.paxChanged.emit(this.camere);
  }
}