import { Component, Input } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import * as detect from 'detect-file-type';
import {
  AlbergoMasterImmagini,
  ImmagineXTipoCameraOTA,
  PortaleXAlbergoImmagini,
  RisorsaImmagini,
  TagCrmImmagini,
} from 'src/app/shared/models/bo.model';

import { BaseComponent } from '../base-component/base-component';

@Component({
  selector: 'app-images-list-edit',
  templateUrl: './images-list-edit.component.html',
  styleUrls: ['./images-list-edit.component.scss']
})
export class ImagesListEditComponent extends BaseComponent {

  @Input() image: PortaleXAlbergoImmagini | AlbergoMasterImmagini | ImmagineXTipoCameraOTA | TagCrmImmagini | RisorsaImmagini;
  @Input() dialogRef: MatDialogRef<{}, any>;

  constructor() {
    super();
  }

  fileSelected(fileSelect: any) {
    let file: File = fileSelect.files == null || fileSelect.files.length == 0 ? null : fileSelect.files[0];

    if (file != null && !!file.type && file.type.startsWith('image/')) {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        let base64 = reader.result.toString();

        if (!!base64) {
          detect.fromBuffer(Uint8Array.from(atob(base64.slice(base64.indexOf(',') + 1)), (c) => c.charCodeAt(0)), (err, result) => {
            if (!err && result != null && !!result.mime && result.mime.startsWith('image/')) {
              this.image.urlImmagine = null;
              this.image.base64String = base64;
            } else {
              fileSelect.value = '';
              this.showError('globals.messages.fileNonValido');
            }
          });
        } else {
          fileSelect.value = '';
          this.showError('globals.messages.fileNonValido');
        }
      }
    } else {
      fileSelect.value = '';
      this.showError('globals.messages.fileNonValido');
    }
  }

  delete() {
    this.showDialogConfirm('globals.eliminareImmagine')
      .then(si => {
        this.image.isDeleted = true;
        this.dialogRef.close(this.image);
      },
        no => { }
      );
  }
}