import { Directive, HostBinding, Input, OnChanges, OnInit } from '@angular/core';
import * as Color from 'color';

@Directive({
  selector: '[appInvertColor]'
})
export class InvertColorDirective implements OnInit, OnChanges {

  @Input('appInvertColor') colore: string;
  @Input() textColor: string;

  @HostBinding('style.color') coloreInvertito: string = '#000000';

  constructor() { }

  ngOnInit() {
    this.coloreInvertito = this.invertColor(true);
  }

  ngOnChanges() {
    this.coloreInvertito = this.invertColor(true);
  }

  invertColor(bw: boolean): string {
    if (this.colore) {
      let color = Color('#FFFFFF');

      try {
        color = Color(this.colore);
      } catch (e) { }

      let r = color.red();
      let g = color.green();
      let b = color.blue();

      if (bw) {
        let txtColor: Color;

        if (!!this.textColor) {
          txtColor = Color(this.textColor);
        }

        if (color.luminosity() <= 0.5) {
          return txtColor != null && txtColor.luminosity() > 0.5 ? txtColor.hex() : '#FFFFFF';
        } else {
          return txtColor != null && txtColor.luminosity() <= 0.5 ? txtColor.hex() : '#000000';
        }
      }

      let ret = Color({ r: 255 - r, g: 255 - g, b: 255 - b });

      return ret.hex();
    }

    return null;
  }
}
