import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { Subject } from 'rxjs';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { ViewSelectDialogComponent } from 'src/app/interfaces/view-select/view-select-dialog/view-select-dialog.component';
import { Albergo, Cliente, Lingua } from 'src/app/shared/models/bo.model';
import {
  AutocompleteDataLoader,
  DialogOptions,
  Filter,
  Sorting,
  WebApiRequestFilter,
  WebApiResponseFilter,
  WebApiResponseUpdate,
} from 'src/app/shared/models/dto';
import {
  CriteriaOperator,
  FilterValueType,
  LettereMaiuscole,
  TipoAnagrafica,
  TipoParametroRicerca,
} from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-cliente-select',
  templateUrl: './cliente-select.component.html',
  styleUrls: ['./cliente-select.component.scss']
})
export class ClienteSelectComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input() dialogRef: MatDialogRef<{}, any>;

  @Input() set cliente(value: Cliente) {
    if (value != null && value.oid > 0) {
      this._cliente = value;
      this.selectedCliente = value;

      if (!!value.nazione) {
        let rq = WebApiRequestFilter.fromFilter('nomeItaliano', CriteriaOperator.Equal, value.nazione, FilterValueType.String);

        this.apiService.getNazioniFilter(rq).subscribe((res: WebApiResponseFilter) => {
          if (res.data.length > 0) {
            value.nazioneObject = res.data[0];
          }
        });
      }
    } else {
      this._cliente = new Cliente();
      this.selectedCliente = new Cliente();
    }
  };

  @Input() linguaDefault: Lingua;

  public _cliente: Cliente = new Cliente();
  public selectedCliente: Cliente = new Cliente();
  public dataLoader: AutocompleteDataLoader;
  public resetSubject: Subject<void> = new Subject();
  public getParametriRicercaMercatoRQ = WebApiRequestFilter.fromFilters([new Filter('tipoParametro', CriteriaOperator.Equal, TipoParametroRicerca.Mercato.toString(), FilterValueType.Number), new Filter('isAttivo', CriteriaOperator.IsTrue)], new Sorting('ordinamento'));
  public getParametriRicercaCanaleVenditaRQ = WebApiRequestFilter.fromFilters([new Filter('tipoParametro', CriteriaOperator.Equal, TipoParametroRicerca.CanaleVendita.toString(), FilterValueType.Number), new Filter('isAttivo', CriteriaOperator.IsTrue)], new Sorting('ordinamento'));
  public tabIndex: number = 0;
  public lettereMaiuscoleAnagrafica: LettereMaiuscole;
  public enumTipoAnagrafica = TipoAnagrafica;

  constructor(public apiService: ApiService,
    private sessionService: SessionService,
    private utilityService: UtilityService,
    public utility: UtilityService) {
    super();
  }

  ngOnInit() {
    if (this._cliente.oid == null || this._cliente.oid <= 0) {
      this._cliente.lingua = this.linguaDefault;
      this.selectedCliente.lingua = this.linguaDefault;
    }

    this.lettereMaiuscoleAnagrafica = this.sessionService.getLettereMaiuscoleAnagrafica();
  }

  save() {
    if (!!this._cliente.cognome && !!this._cliente.email && this._cliente.lingua != null && this._cliente.lingua.oid > 0) {
      if (this.utilityService.validateEmail(this._cliente.email)) {
        if (this._cliente.oid > 0) {
          this.setIsLoading(true);

          this.apiService.setCliente(this._cliente).subscribe((res: WebApiResponseUpdate) => {
            this.setIsLoading(false);
            this.dialogRef.close(res.data.obj);
          });
        } else {
          this.setIsLoading(true);

          this.apiService.getClientiFilter(WebApiRequestFilter.fromFilter('email', CriteriaOperator.Equal, this._cliente.email, FilterValueType.String)).subscribe((clientiRS: WebApiResponseFilter) => {
            if (clientiRS.data.length > 0) {
              this.setIsLoading(false);

              this.showError('sbm.preventivi.emailGiaUtilizzata').then(() => {
                let dialogOptions = new DialogOptions();
                dialogOptions.width = '40vw';
                dialogOptions.disableClose = true;

                let ref = this.showModalDialog(ViewSelectDialogComponent, dialogOptions);
                let component = <ViewSelectDialogComponent>ref.componentInstance;

                component.dialogRef = ref;
                component.title = 'globals.cliente';
                component.data = clientiRS.data;
                component.displayMembers = ['nome', 'cognome', 'email', 'cellulare'];
                component.showFilter = false;

                ref.afterClosed().subscribe((x: Cliente) => {
                  if (x) {
                    this._cliente = x;
                    this.selectedCliente = x;

                    if (!!x.nazione) {
                      let rq = WebApiRequestFilter.fromFilter('nomeItaliano', CriteriaOperator.Equal, x.nazione, FilterValueType.String);

                      this.apiService.getNazioniFilter(rq).subscribe((res: WebApiResponseFilter) => {
                        if (res.data.length > 0) {
                          x.nazioneObject = res.data[0];
                        }
                      });
                    }
                  } else {
                    this._cliente.email = '';
                  }
                });
              });
            } else {
              this._cliente.albergo = new Albergo(this.sessionService.getOidAlbergo());

              this.apiService.setCliente(this._cliente).subscribe((res: WebApiResponseUpdate) => {
                this.setIsLoading(false);
                this.dialogRef.close(res.data.obj);
              });
            }
          });
        }
      } else {
        this.showError('globals.messages.emailNonValida');
      }
    } else {
      this.showError('globals.messages.inserireDati');
    }
  }

  cancel() {
    this.dialogRef.close(new Cliente(-1));
  }

  reset() {
    if (this._cliente.oid == -1) {
      this.resetSubject.next();
    }
  }

  onOptionSelected(cliente: Cliente) {
    this._cliente = cliente;

    if (cliente != null && cliente.oid > 0) {
      if (!!cliente.nazione) {
        let rq = WebApiRequestFilter.fromFilter('nomeItaliano', CriteriaOperator.Equal, cliente.nazione, FilterValueType.String);

        this.apiService.getNazioniFilter(rq).subscribe((res: WebApiResponseFilter) => {
          if (res.data.length > 0) {
            cliente.nazioneObject = res.data[0];
          }
        });
      }
    }
  }

  onUpdateOptions(rq: WebApiRequestFilter) {
    this.dataLoader = new AutocompleteDataLoader(this.apiService.getClientiFilter(rq));
  }

  onNomeChange() {
    if (!!this._cliente.nome) {
      this._cliente.nome = this.onNomeCognomeChange(this._cliente.nome);
    }
  }

  onCognomeChange() {
    if (!!this._cliente.cognome) {
      this._cliente.cognome = this.onNomeCognomeChange(this._cliente.cognome);
    }
  }

  onNomeCognomeChange(value: string): string {
    if (this.lettereMaiuscoleAnagrafica == LettereMaiuscole.Iniziale) {
      return !!value ? (value[0].toUpperCase() + value.substring(1).toLowerCase()) : '';
    } else if (this.lettereMaiuscoleAnagrafica == LettereMaiuscole.TuttoMaiuscolo) {
      return value.toUpperCase();
    }

    return value;
  }

  ngOnDestroy() {
    this.resetSubject.complete();
  }
}